"use client"

import { useState } from "react"
import Link from "next/link"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { lockPayroll, markPayrollPaid } from "@/lib/actions/payroll-actions"
import type { PayrollRun, PayrollItemWithRelations } from "@/lib/types/database"
import { formatCurrency } from "@/lib/utils/format-utils"
import { ArrowLeft, Lock, CheckCircle, Users, Wallet, Calculator } from "lucide-react"

interface PayrollDetailPanelProps {
  payrollRun: PayrollRun
  payrollItems: PayrollItemWithRelations[]
}

export function PayrollDetailPanel({ payrollRun, payrollItems }: PayrollDetailPanelProps) {
  const [loading, setLoading] = useState<"lock" | "paid" | null>(null)

  const handleLock = async () => {
    if (!confirm("Sau khi kha s khng th chnh sa. Bn c chc?")) return
    setLoading("lock")
    await lockPayroll(payrollRun.id)
    setLoading(null)
  }

  const handleMarkPaid = async () => {
    if (!confirm("Xc nhn  thanh ton lng?")) return
    setLoading("paid")
    await markPayrollPaid(payrollRun.id)
    setLoading(null)
  }

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "locked":
        return <Badge className="bg-blue-100 text-blue-800">  kha</Badge>
      case "paid":
        return <Badge className="bg-green-100 text-green-800">  tr</Badge>
      default:
        return <Badge variant="secondary"> Nhp</Badge>
    }
  }

  // Tnh tng
  const totalGross = payrollItems.reduce((sum, item) => sum + (item.total_income || 0), 0)
  const totalDeduction = payrollItems.reduce((sum, item) => sum + (item.total_deduction || 0), 0)
  const totalNet = payrollItems.reduce((sum, item) => sum + (item.net_salary || 0), 0)

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <Button variant="outline" asChild>
          <Link href="/dashboard/payroll">
            <ArrowLeft className="h-4 w-4 mr-2" />
            Quay li
          </Link>
        </Button>
        <div className="flex items-center gap-2">
          {getStatusBadge(payrollRun.status)}
          {payrollRun.status === "draft" && (
            <Button onClick={handleLock} disabled={loading === "lock"} className="gap-2">
              <Lock className="h-4 w-4" />
              {loading === "lock" ? "ang kha..." : "Kha bng lng"}
            </Button>
          )}
          {payrollRun.status === "locked" && (
            <Button
              onClick={handleMarkPaid}
              disabled={loading === "paid"}
              className="gap-2 bg-green-600 hover:bg-green-700"
            >
              <CheckCircle className="h-4 w-4" />
              {loading === "paid" ? "ang x l..." : "nh du  tr"}
            </Button>
          )}
        </div>
      </div>

      {/* Thng k */}
      <div className="grid grid-cols-4 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-2">
              <Users className="h-4 w-4 text-muted-foreground" />
              <span className="text-sm text-muted-foreground">Nhn vin</span>
            </div>
            <p className="text-2xl font-bold mt-1">{payrollItems.length}</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-2">
              <Calculator className="h-4 w-4 text-muted-foreground" />
              <span className="text-sm text-muted-foreground">Tng thu nhp</span>
            </div>
            <p className="text-xl font-bold mt-1 text-blue-600">{formatCurrency(totalGross)}</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-2">
              <span className="text-sm text-muted-foreground">Tng khu tr</span>
            </div>
            <p className="text-xl font-bold mt-1 text-red-600">{formatCurrency(totalDeduction)}</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="flex items-center gap-2">
              <Wallet className="h-4 w-4 text-muted-foreground" />
              <span className="text-sm text-muted-foreground">Tng thc lnh</span>
            </div>
            <p className="text-xl font-bold mt-1 text-green-600">{formatCurrency(totalNet)}</p>
          </CardContent>
        </Card>
      </div>

      {/* Bng chi tit */}
      <Card>
        <CardHeader>
          <CardTitle>Chi tit lng nhn vin</CardTitle>
          <CardDescription>
            K lng thng {payrollRun.month}/{payrollRun.year}
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Nhn vin</TableHead>
                <TableHead className="text-right">Ngy cng</TableHead>
                <TableHead className="text-right">Ngh php</TableHead>
                <TableHead className="text-right">Ngh KL</TableHead>
                <TableHead className="text-right">Lng CB</TableHead>
                <TableHead className="text-right">Ph cp</TableHead>
                <TableHead className="text-right">Thu nhp</TableHead>
                <TableHead className="text-right">Khu tr</TableHead>
                <TableHead className="text-right">Thc lnh</TableHead>
                <TableHead>Ghi ch</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {payrollItems.length === 0 ? (
                <TableRow>
                  <TableCell colSpan={10} className="text-center text-muted-foreground">
                    Cha c d liu
                  </TableCell>
                </TableRow>
              ) : (
                payrollItems.map((item) => (
                  <TableRow key={item.id}>
                    <TableCell>
                      <div>
                        <div className="font-medium">{item.employee?.full_name}</div>
                        <div className="text-sm text-muted-foreground">
                          {item.employee?.employee_code}
                        </div>
                      </div>
                    </TableCell>
                    <TableCell className="text-right">{item.working_days || 0}</TableCell>
                    <TableCell className="text-right">{item.leave_days || 0}</TableCell>
                    <TableCell className="text-right">{item.unpaid_leave_days || 0}</TableCell>
                    <TableCell className="text-right">{formatCurrency(item.base_salary)}</TableCell>
                    <TableCell className="text-right">{formatCurrency(item.allowances)}</TableCell>
                    <TableCell className="text-right text-blue-600">
                      {formatCurrency(item.total_income)}
                    </TableCell>
                    <TableCell className="text-right text-red-600">
                      {formatCurrency(item.total_deduction)}
                    </TableCell>
                    <TableCell className="text-right font-bold text-green-600">
                      {formatCurrency(item.net_salary)}
                    </TableCell>
                    <TableCell>
                      {item.note && (
                        <span className="text-xs text-muted-foreground">{item.note}</span>
                      )}
                    </TableCell>
                  </TableRow>
                ))
              )}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  )
}
